.slider {
  position: relative;
}

.slider__inner {
  position: relative;
  width: 320px;
  margin: 0 auto;
  overflow: hidden;
}

.slider__list {
  @include list-reset;

  position: relative;
  left: 0;
  top: 0;
  display: flex;
  flex-wrap: nowrap;
  transition: all 0.5s ease-in;
}

.slider__item {
  position: relative;
  flex-shrink: 0;
  width: 320px;
}

.slider__image-wrapper {
  display: flex;
  justify-content: center;
}

.slider__photo {
  display: block;
}

.slider__description {
  padding: 25px;
}

.slider__title {
  margin: 0;
  margin-bottom: 20px;
  padding-right: 30px;
  font-family: $font-rubik-bold;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
}

.slider__properties {
  @include list-reset;

  margin-bottom: 20px;
  padding-left: 30px;
  font-size: 15px;
  line-height: 18px;
}

.slider__property {
  position: relative;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: -30px;
    display: block;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background-color: $yellow;
  }
}

.slider__button {
  margin: 0 auto;
}

@media (min-width: $tablet) {
  .slider__inner {
    width: 768px;
  }

  .slider__item {
    display: flex;
    padding-left: 45px;
    width: 768px;
  }

  .slider__photo {
    width: 100%;
    height: 326px;
  }

  .slider__description {
    align-self: center;
    margin-left: -90px;
    width: 345px;
    height: 266px;
    border-radius: 10px;
    background-color: $white;
  }
}

@media (min-width: $desktop) {
  .slider__inner {
    width: 960px;
  }

  .slider__item {
    padding: 0;
    width: 960px;
  }

  .slider__photo {
    height: 404px;
  }
}
