.promo {
  display: flex;
  justify-content: center;
  padding: 50px 20px 0;
  padding-top: 50px;
  min-height: 457px;
  background-color: $yellow;
  background-image: url("../img/promo-mask-mobile.png"), url("../img/promo-cat-mobile.jpg");
  background-size: cover;
  background-position: bottom center;
  background-repeat: no-repeat;

  @media (min-resolution: $retina) {
    background-image: url("../img/promo-mask-mobile.png"), url("../img/promo-cat-mobile-2x.jpg");
  }
}

.promo__inner {
  text-align: center;
}

.promo__city {
  font-size: 12px;
  line-height: 14px;
}

.promo__title {
  margin: 0;
  margin-bottom: 10px;
  font-family: $font-rubik-bold;
  font-weight: bold;
  font-size: 44px;
  line-height: 52px;
}

.promo__text {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}

.promo__button {
  margin: 0 auto;
}

@media (min-width: $tablet) {
  .promo {
    padding-top: 140px;
    min-height: 868px;
    background-image: url("../../img/promo-mask-tablet.png"), url("../../img/promo-cat-tablet.jpg");

    @media (min-resolution: $retina) {
      background-image: url("../img/promo-mask-mobile.png"), url("../img/promo-cat-tablet-2x.jpg");
    }
  }

  .promo__inner {
    padding: 0 20%;
  }

  .promo__city {
    font-size: 14px;
    line-height: 17px;
  }

  .promo__title {
    font-size: 62px;
    line-height: 73px;
  }

  .promo__text {
    font-size: 20px;
    line-height: 24px;
  }
}

@media (min-width: $desktop) {
  .promo {
    justify-content: flex-start;
    padding-top: 140px;
    padding-left: 14%;
    min-height: 504px;
    background-image: url("../../img/promo-mask-desktop.png"), url("../../img/promo-cat-desktop.jpg");

    @media (min-resolution: $retina) {
      background-image: url("../img/promo-mask-desktop.png"), url("../img/promo-cat-desktop-2x.jpg");
      background-position: top left, bottom right;
    }
  }

  .promo__inner {
    padding-left: 0;
    text-align: left;
  }

  .promo__button {
    margin: 0;
  }
}
