.inner-page {
  padding-top: 40px;
  padding-bottom: 70px;

  .suits {
    padding-bottom: 0;

    &::before {
      top: 105px;
    }
  }
}

.inner-page__title {
  margin: 0;
  margin-bottom: 30px;
  font-family: $font-rubik-bold;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
}

.inner-page__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 400px;
  margin: 0 auto;
}

.inner-page__button {
  @include button-reset;

  padding: 0 20px;
  height: 35px;
  border-radius: 30px;
  font-size: 14px;
  line-height: 17px;
  box-shadow: 0 4px 4px rgba($color: #000000, $alpha: 0.1);
}

@media (min-width: $tablet) {
  .inner-page {
    &--catalog {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .inner-page__title {
    margin-right: auto;
  }

  .inner-page__inner {
    margin: 0;
    width: auto;
  }

  .inner-page__button {
    margin-right: 40px;
  }
}

@media (min-width: $desktop) {
  .inner-page {
    &--catalog {
      justify-content: space-between;
    }
  }

  .inner-page__inner {
    position: absolute;
    right: 15%;
    width: auto;
  }

  .inner-page__title {
    width: 100%;
  }
}
